/**
 * Base Carousel Component for Strata UI
 * 
 * Modern CSS scroll-snap carousel with Alpine.js
 * Provides smooth scrolling, touch support, accessibility, and autoplay
 */

import { createBaseComponent, extendComponent } from './BaseComponent.js';
import { dispatchElementEvent, EVENTS } from '../utilities/events.js';

/**
 * Create a Strata Carousel component
 * @param {Object} config - Component configuration
 * @returns {Object} Alpine component configuration
 */
export function createCarouselComponent(config = {}) {
    const baseComponent = createBaseComponent({
        ...config,
        componentName: 'strata-carousel',
        bindProperty: 'currentSlide'
    });

    return extendComponent(baseComponent, {
        // Core state
        slides: [],
        currentSlide: 0,
        totalSlides: 0,
        
        // Configuration
        autoplay: config.autoplay || false,
        interval: config.interval || 3000,
        loop: config.loop !== false,
        itemsPerView: config.itemsPerView || { default: 1 },
        variant: config.variant || 'default',
        size: config.size || 'md',
        
        // Autoplay state
        autoplayEnabled: false,
        autoplayProgress: 0,
        autoplayTimer: null,
        progressTimer: null,
        isPaused: false,
        
        // Touch state
        touchStartX: 0,
        touchStartY: 0,
        touchMoveX: 0,
        touchMoveY: 0,
        touchEndX: 0,
        touchEndY: 0,
        touchDirection: null,
        isTouching: false,
        
        // Visibility state
        showPrevArrow: false,
        showNextArrow: false,
        showProgress: false,
        
        // Intersection observer for performance
        intersectionObserver: null,
        
        // CSS classes from PHP component
        itemClasses: config.itemClasses || 'flex-shrink-0 snap-center w-full',
        
        /**
         * Initialize carousel component
         */
        init() {
            console.log(' Carousel Init - Config:', {
                id: this.id || 'no-id',
                itemsPerView: this.itemsPerView,
                variant: this.variant,
                itemClasses: this.itemClasses
            });
            
            this.$nextTick(() => {
                this.processSlides();
                this.setupScrollListener();
                this.setupIntersectionObserver();
                this.setupKeyboardNavigation();
                this.updateNavigationState();
                this.startAutoplay();
                
                console.log(' Carousel Ready:', {
                    totalSlides: this.totalSlides,
                    currentItemsPerView: this.getCurrentItemsPerView(),
                    containerWidth: this.$refs.scrollContainer?.clientWidth,
                    slideWidth: this.$refs.scrollContainer?.clientWidth / this.getCurrentItemsPerView()
                });
                
                // Dispatch ready event
                this.dispatchComponentEvent(EVENTS.COMPONENT_READY, {
                    totalSlides: this.totalSlides
                });
            });
        },

        /**
         * Process direct children as slides
         */
        processSlides() {
            const container = this.$refs.scrollContainer;
            if (!container) {
                console.error(' Carousel: No scroll container found!');
                return;
            }

            // Get direct children as slides
            const children = Array.from(container.children);
            console.log(' Processing slides:', {
                containerEl: container,
                childrenCount: children.length,
                itemClasses: this.itemClasses
            });
            
            this.slides = children.map((child, index) => ({
                element: child,
                index: index,
                isVisible: false
            }));
            
            this.totalSlides = this.slides.length;
            
            // Apply item classes to each slide with width class replacement
            children.forEach((child, index) => {
                const classesToAdd = this.itemClasses.split(' ');
                console.log(` Processing slide ${index}:`, classesToAdd);
                console.log(` Before classes:`, child.className);
                
                // Remove any existing width classes to avoid conflicts
                const existingClasses = Array.from(child.classList);
                const widthClassesToRemove = existingClasses.filter(cls => 
                    cls.match(/^w-/) || cls.match(/^sm:w-/) || cls.match(/^md:w-/) || cls.match(/^lg:w-/) || cls.match(/^xl:w-/)
                );
                
                if (widthClassesToRemove.length > 0) {
                    console.log(` Removing conflicting width classes:`, widthClassesToRemove);
                    child.classList.remove(...widthClassesToRemove);
                }
                
                // Add new classes
                child.classList.add(...classesToAdd);
                
                console.log(` After classes:`, child.className);
            });
        },

        /**
         * Register slide element when rendered
         */
        registerSlide(index, element) {
            if (this.slides[index]) {
                this.slides[index].element = element;
            }
        },

        /**
         * Setup intersection observer for performance optimization
         */
        setupIntersectionObserver() {
            if (!window.IntersectionObserver) return;

            this.intersectionObserver = new IntersectionObserver(
                (entries) => {
                    entries.forEach(entry => {
                        const slideIndex = parseInt(entry.target.dataset.slideIndex);
                        if (this.slides[slideIndex]) {
                            this.slides[slideIndex].isVisible = entry.isIntersecting;
                        }
                    });
                },
                { 
                    root: this.$refs.scrollContainer,
                    rootMargin: '0px',
                    threshold: 0.5 
                }
            );

            this.addCleanup(() => {
                if (this.intersectionObserver) {
                    this.intersectionObserver.disconnect();
                }
            });
        },

        /**
         * Setup scroll event listener with debouncing
         */
        setupScrollListener() {
            const container = this.$refs.scrollContainer;
            if (!container) return;

            let scrollTimeout;
            const handleScroll = (event) => {
                // Prevent autoplay interruption during programmatic scroll
                if (this.isProgrammaticScroll) return;

                clearTimeout(scrollTimeout);
                scrollTimeout = setTimeout(() => {
                    this.updateCurrentSlideFromScroll();
                    this.updateNavigationState();
                }, 100);
            };

            container.addEventListener('scroll', handleScroll, { passive: true });
            this.addCleanup(() => container.removeEventListener('scroll', handleScroll));
        },

        /**
         * Setup keyboard navigation
         */
        setupKeyboardNavigation() {
            const handleKeydown = (event) => {
                if (!this.$el.contains(document.activeElement)) return;

                switch (event.key) {
                    case 'ArrowLeft':
                        event.preventDefault();
                        this.previousSlide();
                        break;
                    case 'ArrowRight':
                        event.preventDefault();
                        this.nextSlide();
                        break;
                    case 'Home':
                        event.preventDefault();
                        this.goToSlide(0);
                        break;
                    case 'End':
                        event.preventDefault();
                        this.goToSlide(this.totalSlides - 1);
                        break;
                    case ' ':
                    case 'Enter':
                        if (event.target.hasAttribute('data-carousel-control')) {
                            event.preventDefault();
                            event.target.click();
                        }
                        break;
                }
            };

            document.addEventListener('keydown', handleKeydown);
            this.addCleanup(() => document.removeEventListener('keydown', handleKeydown));
        },

        /**
         * Update current slide based on scroll position
         */
        updateCurrentSlideFromScroll() {
            const container = this.$refs.scrollContainer;
            if (!container || this.slides.length === 0) return;

            const scrollLeft = container.scrollLeft;
            const containerWidth = container.clientWidth;
            const itemsPerView = this.getCurrentItemsPerView();
            const slideWidth = containerWidth / itemsPerView;
            
            let newCurrentSlide = Math.round(scrollLeft / slideWidth);
            newCurrentSlide = Math.max(0, Math.min(newCurrentSlide, this.totalSlides - 1));
            
            if (newCurrentSlide !== this.currentSlide) {
                const previousSlide = this.currentSlide;
                this.currentSlide = newCurrentSlide;
                
                this.dispatchComponentEvent(EVENTS.CHANGE, { 
                    currentSlide: this.currentSlide,
                    previousSlide: previousSlide,
                    totalSlides: this.totalSlides 
                });
                
                this.$dispatch('carousel-change', {
                    currentSlide: this.currentSlide,
                    previousSlide: previousSlide,
                    totalSlides: this.totalSlides
                });
            }
        },

        /**
         * Get current items per view based on responsive config
         */
        getCurrentItemsPerView() {
            if (typeof this.itemsPerView === 'number') {
                console.log(' ItemsPerView is number:', this.itemsPerView);
                return this.itemsPerView;
            }

            // Simple responsive implementation
            // In a production app, you'd want proper breakpoint detection
            const width = window.innerWidth;
            let result;
            
            if (width >= 1024 && this.itemsPerView.lg) {
                result = this.itemsPerView.lg;
            } else if (width >= 768 && this.itemsPerView.md) {
                result = this.itemsPerView.md;
            } else if (width >= 640 && this.itemsPerView.sm) {
                result = this.itemsPerView.sm;
            } else {
                result = this.itemsPerView.default || 1;
            }
            
            console.log(' Responsive ItemsPerView calculation:', {
                windowWidth: width,
                itemsPerViewConfig: this.itemsPerView,
                selectedValue: result,
                breakpoint: width >= 1024 ? 'lg' : width >= 768 ? 'md' : width >= 640 ? 'sm' : 'default'
            });
            
            return result;
        },

        /**
         * Update navigation button states
         */
        updateNavigationState() {
            const canGoPrev = this.loop || this.currentSlide > 0;
            const canGoNext = this.loop || this.currentSlide < this.totalSlides - 1;
            
            this.showPrevArrow = canGoPrev && this.totalSlides > 1;
            this.showNextArrow = canGoNext && this.totalSlides > 1;
        },

        /**
         * Go to specific slide with smooth animation
         */
        goToSlide(index, options = {}) {
            if (index < 0 || index >= this.totalSlides) return;
            
            const container = this.$refs.scrollContainer;
            if (!container) return;

            const itemsPerView = this.getCurrentItemsPerView();
            const slideWidth = container.clientWidth / itemsPerView;
            const targetScrollLeft = index * slideWidth;
            
            // Mark as programmatic scroll to prevent event conflicts
            this.isProgrammaticScroll = true;
            
            container.scrollTo({
                left: targetScrollLeft,
                behavior: options.smooth !== false ? 'smooth' : 'auto'
            });

            // Reset programmatic scroll flag after animation
            setTimeout(() => {
                this.isProgrammaticScroll = false;
            }, 500);

            const previousSlide = this.currentSlide;
            this.currentSlide = index;
            
            this.dispatchComponentEvent(EVENTS.CHANGE, {
                currentSlide: this.currentSlide,
                previousSlide: previousSlide,
                totalSlides: this.totalSlides,
                programmatic: true
            });

            this.resetAutoplay();
            this.updateNavigationState();
        },

        /**
         * Go to next slide
         */
        nextSlide() {
            const nextIndex = this.currentSlide + 1;
            if (nextIndex >= this.totalSlides) {
                if (this.loop) {
                    this.goToSlide(0);
                }
            } else {
                this.goToSlide(nextIndex);
            }
        },

        /**
         * Go to previous slide
         */
        previousSlide() {
            const prevIndex = this.currentSlide - 1;
            if (prevIndex < 0) {
                if (this.loop) {
                    this.goToSlide(this.totalSlides - 1);
                }
            } else {
                this.goToSlide(prevIndex);
            }
        },

        /**
         * Start autoplay functionality
         */
        startAutoplay() {
            if (!this.autoplay || this.totalSlides <= 1) return;
            
            this.autoplayEnabled = true;
            this.showProgress = true;
            this.scheduleNextSlide();

            this.dispatchComponentEvent('carousel-autoplay-start');
        },

        /**
         * Stop autoplay functionality
         */
        stopAutoplay() {
            this.autoplayEnabled = false;
            this.showProgress = false;
            this.clearAutoplayTimer();
            this.clearProgressTimer();

            this.dispatchComponentEvent('carousel-autoplay-stop');
        },

        /**
         * Schedule next slide for autoplay
         */
        scheduleNextSlide() {
            if (this.isPaused || !this.autoplayEnabled) return;

            this.autoplayProgress = 0;
            
            // Progress animation
            this.progressTimer = setInterval(() => {
                if (this.isPaused || !this.autoplayEnabled) return;
                
                this.autoplayProgress += (100 / (this.interval / 100));
                if (this.autoplayProgress >= 100) {
                    this.clearProgressTimer();
                }
            }, 100);

            // Next slide timer
            this.autoplayTimer = setTimeout(() => {
                if (this.autoplayEnabled && !this.isPaused) {
                    this.nextSlide();
                    this.scheduleNextSlide();
                }
            }, this.interval);
        },

        /**
         * Reset autoplay timers
         */
        resetAutoplay() {
            if (!this.autoplayEnabled) return;
            
            this.clearAutoplayTimer();
            this.clearProgressTimer();
            
            if (!this.isPaused) {
                this.scheduleNextSlide();
            }
        },

        /**
         * Pause autoplay
         */
        pauseAutoplay() {
            if (!this.autoplayEnabled) return;
            
            this.isPaused = true;
            this.clearAutoplayTimer();
            this.clearProgressTimer();
            
            this.dispatchComponentEvent('carousel-autoplay-pause');
        },

        /**
         * Resume autoplay
         */
        resumeAutoplay() {
            if (!this.autoplayEnabled || !this.isPaused) return;
            
            this.isPaused = false;
            this.scheduleNextSlide();
            
            this.dispatchComponentEvent('carousel-autoplay-resume');
        },

        /**
         * Clear autoplay timer
         */
        clearAutoplayTimer() {
            if (this.autoplayTimer) {
                clearTimeout(this.autoplayTimer);
                this.autoplayTimer = null;
            }
        },

        /**
         * Clear progress timer
         */
        clearProgressTimer() {
            if (this.progressTimer) {
                clearInterval(this.progressTimer);
                this.progressTimer = null;
            }
        },

        /**
         * Handle touch start event
         */
        handleTouchStart(event) {
            this.isTouching = true;
            this.touchStartX = event.touches[0].clientX;
            this.touchStartY = event.touches[0].clientY;
            this.touchMoveX = this.touchStartX;
            this.touchMoveY = this.touchStartY;
            this.touchDirection = null;
            this.pauseAutoplay();
        },

        /**
         * Handle touch move event
         */
        handleTouchMove(event) {
            if (!this.isTouching) return;
            
            this.touchMoveX = event.touches[0].clientX;
            this.touchMoveY = event.touches[0].clientY;
            
            // Determine swipe direction early to prevent/allow scrolling
            const deltaX = Math.abs(this.touchMoveX - this.touchStartX);
            const deltaY = Math.abs(this.touchMoveY - this.touchStartY);
            
            // Only set direction once we have meaningful movement
            if (deltaX > 10 || deltaY > 10) {
                if (deltaX > deltaY && !this.touchDirection) {
                    // Horizontal swipe - prevent default to avoid page scroll
                    this.touchDirection = 'horizontal';
                    event.preventDefault();
                } else if (deltaY > deltaX && !this.touchDirection) {
                    // Vertical swipe - allow default behavior for page scroll
                    this.touchDirection = 'vertical';
                }
            }
            
            // Continue preventing horizontal scroll once direction is determined
            if (this.touchDirection === 'horizontal') {
                event.preventDefault();
            }
        },

        /**
         * Handle touch end event
         */
        handleTouchEnd(event) {
            if (!this.isTouching) return;
            
            this.isTouching = false;
            this.touchEndX = event.changedTouches[0].clientX;
            this.touchEndY = event.changedTouches[0].clientY;
            
            // Only handle swipe if it was horizontal
            if (this.touchDirection === 'horizontal') {
                this.handleSwipeGesture();
            }
            
            // Resume autoplay after a delay
            setTimeout(() => {
                this.resumeAutoplay();
            }, 1000);
        },

        /**
         * Handle swipe gesture
         */
        handleSwipeGesture() {
            const swipeThreshold = 30; // Reduced threshold for better responsiveness
            const diff = this.touchStartX - this.touchEndX;
            const distance = Math.abs(diff);

            if (distance > swipeThreshold) {
                if (diff > 0) {
                    this.nextSlide();
                } else {
                    this.previousSlide();
                }
                
                this.dispatchComponentEvent('carousel-swipe', {
                    direction: diff > 0 ? 'left' : 'right',
                    distance: distance
                });
            }
        },

        /**
         * Handle scroll event
         */
        handleScroll(event) {
            // This is called from the template
            if (!this.isProgrammaticScroll) {
                this.pauseAutoplay();
            }
        },

        /**
         * Get item CSS classes
         */
        getItemClasses() {
            // This will be populated from the PHP component
            return this.itemClasses || 'flex-shrink-0 snap-center w-full';
        },


        /**
         * Check if can go to previous slide
         */
        get canGoPrevious() {
            return this.loop || this.currentSlide > 0;
        },

        /**
         * Check if can go to next slide
         */
        get canGoNext() {
            return this.loop || this.currentSlide < this.totalSlides - 1;
        },

        /**
         * Get carousel statistics
         */
        get stats() {
            return {
                currentSlide: this.currentSlide,
                totalSlides: this.totalSlides,
                isAutoplayActive: this.autoplayEnabled && !this.isPaused,
                canGoPrevious: this.canGoPrevious,
                canGoNext: this.canGoNext
            };
        },

        /**
         * Component cleanup
         */
        destroy() {
            this.clearAutoplayTimer();
            this.clearProgressTimer();
            
            if (this.intersectionObserver) {
                this.intersectionObserver.disconnect();
            }
        }
    });
}

export default {
    createCarouselComponent
};